ure' | 'full'>('pure');\n\nconst reversedSelectedMks = computed(() => {\n  return props.latestData?.selectedMks?.slice().reverse() ?? [];\n});\n\nconst displayData = computed<DisplayData | null>(() => {\n  if (selectedMk.value === latestMkFromProps.value) {\n    return props.latestData;\n  }\n  return historicalSnapshot.value;\n});\n\nwatch(\n  () => latestMkFromProps.value,\n  newLatestMk => {\n    if (selectedMk.value !== newLatestMk) {\n      selectedMk.value = newLatestMk;\n      historicalSnapshot.value = null;\n    }\n    error.value = null;\n    loading.value = false;\n  },\n);\n\nconst handleSelectionChange = () => {\n  const mk = selectedMk.value;\n\n  if (mk === latestMkFromProps.value) {\n    historicalSnapshot.value = null;\n    error.value = null;\n    loading.value = false;\n    return;\n  }\n\n  if (mk) {\n    loading.value = true;\n    error.value = null;\n    historicalSnapshot.value = null;\n    logger.debug('SnapshotManager', `mk: ${mk}`);\n    eventEmit('era:getSnapshotAtMk', { mk });\n  }\n};\n\nconst handleQueryResult = (detail: QueryResultPayload) => {\n  if (detail.queryType === 'getSnapshotAtMk' && detail.request.mk === selectedMk.value) {\n    loading.value = false;\n    if (detail.result) {\n      historicalSnapshot.value = detail.result as QueryResultItem;\n      error.value = null;\n    } else {\n      error.value = ` mk \"${selectedMk.value}\" `;\n      historicalSnapshot.value = null;\n    }\n  }\n};\n\nconst formatMkForDisplay = (mk: string | null, index: number): string => {\n  const total = reversedSelectedMks.value.length;\n  const msgId = total - 1 - index;\n  const isLatest = mk === latestMkFromProps.value;\n\n  if (!mk) return ` ${msgId} (MK)`;\n\n  const shortMk = mk.substring(mk.length - 6);\n  const label = ` ${msgId} (...${shortMk})`;\n\n  return isLatest ? `${label} ()` : label;\n};\n\nonMounted(() => {\n  eventOn('era:queryResult', handleQueryResult);\n});\n\nonUnmounted(() => {\n  eventRemoveListener('era:queryResult', handleQueryResult);\n});\n</script>\n\n<!--   scoped #era-snapshot-ui  -->\n<style>\n/* =========================\n   ERA Snapshot \n    #era-snapshot-ui \n   ========================= */\n\n/* ----  ---- */\n#era-snapshot-ui.snapshot-manager {\n  /*  */\n  display: flex; /*  */\n  flex-direction: column; /*  */\n  gap: 12px; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .selector-wrapper {\n  /*  */\n  display: block; /*  */\n  margin-bottom: 4px; /*  */\n}\n#era-snapshot-ui .select-field {\n  /*  */\n  position: relative; /*  */\n  display: block; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .selector-label {\n  /* :  */\n  font-size: 14px; /*  */\n  font-weight: 700; /*  */\n  color: var(--text-title); /*  */\n  flex-shrink: 0; /*  */\n  letter-spacing: 0.2px; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .select-container {\n  /*  */\n  position: relative; /*  */\n  flex-grow: 1; /*  */\n  isolation: isolate; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .select-container::after {\n  /*  */\n  content: ''; /*  */\n  position: absolute; /*  */\n  top: 50%; /*  */\n  right: 12px; /*  */\n  transform: translateY(-50%) rotate(0deg); /*  */\n  font-size: 14px; /*  */\n  color: var(--snapshot-muted); /*  */\n  pointer-events: none; /*  */\n  transition:\n    transform 0.18s ease,\n    color 0.2s ease; /*  */\n  z-index: 1; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .select-container:focus-within::after {\n  /*  */\n  transform: translateY(-50%) rotate(180deg); /*  */\n  color: var(--snapshot-accent); /*  */\n}\n\n/* ----  +  +  ---- */\n#era-snapshot-ui .mk-selector {\n  /*  */\n  width: 100%; /*  */\n  padding: 10px 36px 10px 12px; /*  */\n  border-radius: 10px; /*  */\n  border: 1px solid var(--snapshot-stroke); /*  */\n  background: var(--snapshot-bg-gradient), var(--snapshot-bg); /*  */\n  backdrop-filter: saturate(1.05) blur(2px); /*  */\n  color: var(--snapshot-text); /*  */\n  font-family: inherit; /*  */\n  font-size: 14px; /*  */\n  line-height: 1.2; /*  */\n  cursor: pointer; /*  */\n  -webkit-appearance: none; /* Safari/Chrome */\n  -moz-appearance: none; /* Firefox */\n  appearance: none; /*  */\n  transition:\n    border-color 0.18s ease,\n    box-shadow 0.18s ease,\n    background 0.25s ease; /*  */\n  box-shadow: var(--snapshot-inner-shadow), var(--snapshot-outer-shadow); /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .mk-selector:hover {\n  /*  */\n  border-color: color-mix(in oklab, var(--snapshot-stroke), var(--snapshot-accent) 22%); /*  */\n}\n\n/* ---- / +  ---- */\n#era-snapshot-ui .mk-selector:focus {\n  /*  */\n  outline: none; /*  */\n  border-color: var(--snapshot-accent); /*  */\n  box-shadow:\n    0 0 0 3px var(--snapshot-accent-soft),\n    /*  */ var(--snapshot-focus-inner-shadow); /*  */\n}\n#era-snapshot-ui .mk-selector:focus-visible {\n  /*  */\n  outline: none; /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .mk-selector option {\n  /*  */\n  color: var(--snapshot-text); /*  */\n  background: var(--snapshot-bg); /*  */\n}\n\n/* ----  /  ---- */\n#era-snapshot-ui .loading-indicator,\n#era-snapshot-ui .error-message {\n  /*  */\n  padding: 18px; /*  */\n  text-align: center; /*  */\n  border-radius: 12px; /*  */\n  border: 1px dashed var(--line, rgba(0, 0, 0, 0.12)); /*  */\n  background: var(--bg-empty, rgba(0, 0, 0, 0.03)); /*  */\n  color: var(--text-normal, #2a2a2a); /*  */\n}\n\n/* ----  ---- */\n#era-snapshot-ui .error-message {\n  /*  */\n  color: var(--text-error, #a81818); /*  */\n  background: var(--bg-error-soft, rgba(235, 68, 90, 0.08)); /*  */\n  border-color: var(--border-error, rgba(235, 68, 90, 0.45)); /*  */\n}\n\n/* ----  ---- */\n@media (prefers-reduced-motion: reduce) {\n  #era-snapshot-ui .select-container::after,\n  #era-snapshot-ui .mk-selector {\n    transition: none; /*  */\n  }\n}\n\n/*    */\n#era-snapshot-ui .field-label {\n  /*  */\n  position: absolute; /*  */\n  top: 0; /*  */\n  left: 12px; /*  */\n  transform: translateY(-50%); /*  */\n  padding: 0 6px; 